import React, { Component } from 'react';
import { Collapse, CardImg, Button, CardBody, Card } from 'reactstrap';
import { baseUrl } from '../shared/baseUrl';

const defaultToppingOption = "Regular"

class OrderStatus extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isOpen: false,
            isSubOpen: [false]
        };
    }

    componentDidMount() {
        let mql = window.matchMedia('(max-width: 992px)');
        const listenerFunc = () => {
            if (mql.matches) {
                this.setState({isOpen: false});
            }
            else {
                this.setState({isOpen: true});
            }
        }
        listenerFunc();
        mql.addListener(listenerFunc);
    }

    displayCurrentOrder() {
        let displayToppings = null;
        let currentOrderToppings = this.props.currentOrderToppings;
        let condiments = this.props.condiments;
        displayToppings = Object.keys(currentOrderToppings).map(function(key, index) {
            if (currentOrderToppings[key]) {
                let orderModifier = "";
                if (currentOrderToppings[key] !== defaultToppingOption) {
                    orderModifier = currentOrderToppings[key] + " ";
                }
                return <div key={key}>
                            <CardImg style={{width: '16px', height: '16px'}} src={baseUrl + condiments[index].image} alt={condiments[index].name} />
                            {orderModifier}{key}
                            <span className="float-right">{condiments[index].calories}</span>
                    </div>
            }
            return '';
        });
        return <>
                <h5>
                    <CardImg style={{width: '32px', height: '32px'}} src={baseUrl + this.props.currentOrder.image} alt={this.props.currentOrder.name} />{this.props.currentOrder.name}
                </h5>
                <div><h5>Toppings
                    <span className="float-right">Calories</span></h5>
                </div>
                {displayToppings}
            </>;
    }

    displayWholeOrder() {
        let displayFoodItems = '';
        displayFoodItems = (this.props.fullOrder).map((key, index) => {
            let toggleButtonText = "";
            let displayToppings = "";
            let currentOrderToppings = key.toppings;
            let condiments = this.props.condiments;
            let counter = 0;
                //THIS WILL WANT TO BE REMOVED IF WE HAVE EXTRA INFO FOR EACH ITEM... WHICH WE PROBABLY SHOULD HAVE
            if(key.toppings) {
                toggleButtonText = ((this.state.isSubOpen[index]) ? <span className="fa fa-angle-down fa-lg"></span> : <span className="fa fa-angle-up fa-lg"></span>)
                displayToppings = Object.keys(currentOrderToppings).map(function(key, index) {
                    if (currentOrderToppings[key]) {
                        let orderModifier = "";
                        if (currentOrderToppings[key] !== defaultToppingOption) {
                            orderModifier = currentOrderToppings[key] + " ";
                        }
                        if (counter > 0) {
                            counter++;
                            return <span key={key}>
                            , <CardImg style={{width: '16px', height: '16px'}} src={baseUrl + condiments[index].image} alt={condiments[index].name} />{orderModifier}{key}</span>
                        }
                        else {
                            counter++;
                            return <span key={key}>
                                    <CardImg style={{width: '16px', height: '16px'}} src={baseUrl + condiments[index].image} alt={condiments[index].name} />
                                    {orderModifier}{key}</span>
                        }
                        
                    }
                    return '';
                });
            }
            return <div key={index}>
                <h5 className="outline test" onClick={() => {
                    let newState = this.state.isSubOpen;
                    newState[index] = !newState[index];
                    this.setState({isSubOpen: newState})}} >
                        
                        <CardImg style={{width: '32px', height: '32px'}} src={baseUrl + key.food.image} alt={key.food.food} />{key.food.name}{toggleButtonText}
                        
                    </h5>
                <Collapse isOpen={this.state.isSubOpen[index]}>{displayToppings}</Collapse>
            </div>;
            });
        return displayFoodItems
    }

    displayOrder() {
        let mainContent, mainButton = null;
        //if editing an order
        if (this.props.currentOrder) {
            mainContent = this.displayCurrentOrder();
            mainButton = <Button color="primary" onClick={this.props.addToOrder}>Add to order</Button>
        }
        //if in the main
        else if (this.props.fullOrder.length > 0) {
            mainContent = this.displayWholeOrder();
            mainButton = <Button color="primary" onClick={this.props.submitOrder}>Finalize Order</Button>
        }

        let price = "0.00";
        if (this.props.currentOrder) {
            price = this.props.currentOrder.price;
        }
        else if (this.props.fullOrder.length > 0) {
            this.props.fullOrder.map((item, key) => {
                price = +price + +this.props.fullOrder[key].food.price;
            });
        }
        price = (+price + 0).toFixed(2);
        let tax = ((Math.round(+price * +this.props.salesTax * 100))/100).toFixed(2);
        return <CardBody> 
            <h5>Your order</h5>
            <div className="row">
                <div className="col-xs-12 col-sm-7 col-md-8 col-lg-12">
                    {mainContent}
                </div>
                <div className="col-xs-12 col-sm-5 col-md-4 col-lg-12">
                    <div className="row">
                        <div className="col-8">Subtotal</div>
                        <div className="col-4">${price}</div>
                        <div className="col-8">Estimated Tax</div>
                        <div className="col-4">${tax}</div>
                        <div className="col-8">Total</div>
                        <div className="col-4">${(+price + +tax).toFixed(2)}</div>
                    </div>
                </div>
            </div>
            <div className="row pt-4 text-right">
                <div className="col-12">
                    {mainButton}
                </div>
            </div>
        </CardBody>
    }

    render() {

        const toggleButtonText = ((this.state.isOpen) ? "Hide order" : "View order")
        const displayOrder = this.displayOrder();
        
        return (
            <div className="orderStatus">
                <Collapse isOpen={this.state.isOpen}>
                    <Card>
                        {displayOrder}
                    </Card>
                </Collapse>
                <div className="text-right orderStatusToggleButton">
                    <Button color="outline" onMouseDown={e => e.preventDefault()} onClick={() => this.setState({isOpen: !this.state.isOpen})} >{toggleButtonText}</Button>
                </div>
            </div>
        );
    } 
}

export default OrderStatus;