import React from 'react';
import MenuItem from './menuItemComponent';

function constructBurger(props) {
    const condiments = props.condiments.map((item, key) => {
        if (item.category === "condiment") {
            return <MenuItem
                    key={key+"condiment"}
                    title={item.name}
                    subtitle={item.calories + " cal"}
                    options={[
                        {value: "Light", onClick: () => props.selectTopping(item.name, "Light")},
                        {value: "Regular", onClick: () => props.selectTopping(item.name, "Regular")},
                        {value: "Extra", onClick: () => props.selectTopping(item.name, "Extra")}
                    ]}
                    image={item.image}
                    checkable={true}
                    checked={props.toppings[item.name]}
                    onClick={() => props.selectTopping(item.name, !props.toppings[item.name])}
                />
        }
        return "";
    })

    const sauces = props.condiments.map((item, key) => {
        if (item.category === "sauce") {
            return <MenuItem
                    key={key+"sauce"}
                    title={item.name}
                    subtitle={item.calories + " cal"}
                    options={[
                        {value: "Light", onClick: () => props.selectTopping(item.name, "Light")},
                        {value: "Regular", onClick: () => props.selectTopping(item.name, "Regular")},
                        {value: "Extra", onClick: () => props.selectTopping(item.name, "Extra")}
                    ]}
                    image={item.image}
                    checkable={true}
                    checked={props.toppings[item.name]}
                    onClick={() => props.selectTopping(item.name, !props.toppings[item.name])}
                />
        }
        return "";
    })
        return <>
                <div className="row">
                    <div className="col-12">
                        <h4>Condiments</h4>
                    </div>
                    {condiments}
                </div>
                <div className="row">
                    <div className="col-12">
                        <h4>Sauces</h4>
                    </div>
                    {sauces}
                </div>
            </>
}

export default constructBurger;